/* antd icons */


.switch-icon{
  border: 5px solid rgb(6, 179, 248);
  border-radius: 1px;
  padding: 10px 15px;
}
.anticon svg{
   font-size: 20px;
   cursor: pointer;
}

.active-icon{
  color:rgb(92, 247, 92);
}
.inactiv-icon{
  color: rgba(255, 65, 27, 0.861);
}

.filter-tab .ant-select-selector {
  width: 150px !important;
}
